
body {
    height:100%;
}
.app{
    height: 100vh;

    .sections{
        width: 100%;
        height: calc(100vh - 70px);
        background-color: black;
        position: relative ;
        top: 70px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width: none; //hide scrollbar for firefox
   
        &::-webkit-scrollbar{//hide scrollbar for others browser(chrom, safari..)
            display: none;
        }

        > *{
            overflow-x: hidden;
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
}